import React, {Component} from 'react';

export default class Login extends Component {
    render(){
        return (
            <div className="gallery-page">
                <img className="big-logo" src={require('images/logo_blue.png')}/>
                <img className="login-form" src={require('images/login/login_form.png')}/>
                <img className="register" src={require('images/login/register.png')}/>
            </div>
        )
    }
}


