import React, {Component} from 'react';
import TopBar from './TopBar';


export default class Gallery extends Component {
    render(){
        return (
            <div className="gallery-page">
                <TopBar theme = 'blue'/>
                <div className="menu-circle"></div>
            </div>
        )
    }
}


